* {
    box-sizing: border-box;
}

body {
    background-color: #eff7f2;
    margin: 0;
    padding: 0;
}

html,
body,
#root,
.container {
    display: flex;
    justify-content: center;
    height: 100%;
}

.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;    
    font-weight: 300;
}

.movies__title {
    width: 100%;
    padding: 20px;
    font-size: 36px;
    color: #2c2c2c;
    margin-bottom: 90px;
    background: #fff;
    box-shadow: 0 13px 27px -5px rgb(50 50 93 / 25%), 0 8px 16px -8px rgb(0 0 0 / 30%), 0 -6px 16px -6px rgb(0 0 0 / 3%);
}
